.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-image: url('https://data.whicdn.com/images/356058336/original.jpg');
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  font-weight: 700;
  color: black;
}

.TypeWriterEffect {
 animation: 
    typing 3.5s steps(40, end),
    blink-caret .75s step-end infinite;
}

.App-link {
  color: #000000;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
  
 @keyframes typing {
  from { 
    width: 0 
  }
  to { 
    width: 100% 
  }
}

@keyframes blink-caret {
  from, to { 
    border-color: transparent 
  }
  50% { 
    border-color: orange; 
  }
}
}